.axisVoters line, .axisVoters path, .lineVoters {
    stroke: steelblue;
}

.axisNbrReps line, .axisNbrReps path, .lineNbrReps {
    stroke: red;
}

.axisVoters text, .axisVotersLabel {
    fill: steelblue;
    font-size: 0.75rem;
}

.axisNbrReps text, .axisNbrRepsLabel {
    fill: red;
    font-size: 0.75rem;
}

.lineMissing {
    stroke: #ccc;
}